import React, { useEffect, useState } from 'react'
import AddIcon from '@mui/icons-material/Add';
import DescriptionIcon from '@mui/icons-material/Description';
import RefreshIcon from '@mui/icons-material/Refresh';
import DeleteIcon from '@mui/icons-material/DeleteForever';
import RemoveCircleOutlineTwoToneIcon from '@mui/icons-material/RemoveCircleOutlineTwoTone';
import { Button, Stack, TextField ,IconButton } from '@mui/material';

const getLocalItems=()=>{
    let list= localStorage.getItem('list');
    if (list){
        return JSON.parse(localStorage.getItem('list'))
    }else{
        return [];
    }
}

function App(){
    const [notein,getNote] = useState('')
    const [noteed,setNote] = useState(getLocalItems())

    const Adding=()=>{
        if (notein){
        const dic = {id:new Date().getTime(),name:notein,status:'not-done'}
        setNote([dic,...noteed])
        getNote('')
        }
    }
    const done=(id)=>{
        const l = getLocalItems()
        if (l[id].status==='not-done'){
            l[id].status='done'
            setNote(l)
        }else{
            l[id].status='not-done'
            setNote(l)
        }
    }
    
    const Delete=(id)=>{
        const updatedItems= noteed.filter((elem,ind)=>{
            return ind!==id;
        })
        setNote(updatedItems)
    }
    const Remove=()=>{
        setNote([])
    }
    useEffect(()=>{
        localStorage.setItem('list',JSON.stringify(noteed))
        },[noteed])

    return(
        <>
        <div className='main-container'>
                
                <div className='nav'>
                    <DescriptionIcon/>
                    <h1>TODO</h1>
                 </div>
                <Stack direction='row' className='add'>
                    <TextField id="filled-basic" label=' add a note' color='primary' size='small' value={notein} onChange={(e)=>{
                        getNote(e.target.value)
                    }}></TextField>
                    {/* <input type="text" placeholder=' add a note' value={notein} onChange={(e)=>{
                        getNote(e.target.value)
                    }} /> */}
                    <IconButton aria-label="add"  variant="contained" color="success" size="small" onClick={Adding} ><AddIcon  /></IconButton>
                </Stack>
                
                <div className='notes'>
                        {
                        noteed.map((elem,ind)=>{
                            return (
                                <div className="eachItems"  key={elem.id}>
                                    {elem.status==='done'? <RefreshIcon className='regain' onClick={ ()=>{ done(ind)} } />:<RemoveCircleOutlineTwoToneIcon  onClick={ ()=>{ done(ind)} } />}
                                    <div className={elem.status}  >{elem.name}</div>
                                    <DeleteIcon onClick={ ()=>{ Delete(ind)} } />
                                </div>
                            )
                        })
                    }
                    
                    
                </div>
                <div id='remove'>
                    <Button variant='contained' color='primary' onClick={Remove} >Remove All</Button>
                </div>
                
        </div>
        </>
    )
}

export default App











